import { ClrDatagridStringFilterInterface } from "@clr/angular";

//Models
import { Todo } from '../../app/models/todo';
import { Person } from '../../app/models/person'

/**
* Class for filtering Field metadata in datagrids on filterType property of model
*/
export class NameFilter implements ClrDatagridStringFilterInterface<Todo> {
    accepts(todo: Todo, search: string): boolean {
        return todo.employeeName.toLowerCase().indexOf(search) >= 0;
    }
}

export class EmployeeNameFilter implements ClrDatagridStringFilterInterface<Person> {
  accepts(person: Person, search: string): boolean {
    console.log('here');
    return (person.firstname + " " + person.lastname).toLocaleLowerCase().indexOf(search.trim().toLocaleLowerCase()) >= 0;
  }
}
