import { Component, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Observable, interval } from 'rxjs';
import { Todo } from './../app/models/todo';
import { Person } from './../app/models/person';
import { RedisInfo } from './../app/models/redisinfo';
import { PizzaTrackerTimeline } from './../app/models/pizzatrackertimline';
import { Router } from '@angular/router';
import { DataService } from './../Shared/Services/data.service';
import { take, catchError, map } from 'rxjs/operators';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { environment } from './../environments/environment'
import { SignalRService } from './../Shared/Services/data.service';
import { HttpClient } from '@angular/common/http';
import '@clr/icons';
import '@clr/icons/shapes/all-shapes';
import { startWith, switchMap } from "rxjs/operators";
import { NameFilter, EmployeeNameFilter } from "../Shared/Util/utilFilters";



@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})

export class AppComponent {

    public nameFilter = new NameFilter();
    public createdByFilter = new NameFilter();
    public empNameFilter = new EmployeeNameFilter();

    title = 'Killer Demo';
    public env;

    public errorMessage: string = '';
    public href: string = "";

    public todoItems: Observable<Todo[]>;
    public ptl: Observable<PizzaTrackerTimeline[]>;
    public redisInfo: Observable<RedisInfo>;
    public loading: any;
    public ptld: any;
    public quickAddTodo: any;
    public quickAddAddress: any;
    public fullListTodo: any;
    public fullListAddress: any;
    public AddressItems: Observable<Person[]>;



    public todoForm: FormGroup;
    public addressForm: FormGroup;
    public lastBuiltID: number = 0;
    public contld: any;
    public buildRunning: boolean = false;
    public lastRunBuildDetails: any;

    public technicalInformation: any;

    private fullNameSelected = "";

    ngOnInit() {
        this.signalRService.startConnection();
        this.signalRService.addTransferKafkaDataListener();
        this.startHttpRequest();
    }


    constructor(@Inject(DOCUMENT) private document: Document,
        private dataService: DataService, private router: Router,
        private httpClient: HttpClient,
        public signalRService: SignalRService) {

        this.todoForm = new FormGroup({
            todoText: new FormControl(null, Validators.required),
            dueDate: new FormControl(null, Validators.required),
            createdBy: new FormControl(null, Validators.required),
            employeeName: new FormControl(null),
            id: new FormControl(''),
            createdDate: new FormControl(''),
            employeeId: new FormControl(null, Validators.required),
        });

        this.addressForm = new FormGroup({
            firstname: new FormControl(null, Validators.required),
            lastname: new FormControl(null, Validators.required),
            streetaddress: new FormControl(null, Validators.required),
            city: new FormControl(null, Validators.required),
            state: new FormControl(null, Validators.required),
            zip: new FormControl(null, Validators.required),
            phone: new FormControl(null),
            email: new FormControl(null),
            profilepicture: new FormControl(''),
            createdon: new FormControl(''),
            id: new FormControl('')
        });
        this.populateAddresses();
        this.populateTodoFirstLoad();
        this.getPizzatrackertimeline()

        this.href = document.location.protocol + '//' + document.location.hostname + ':' + document.location.port;
        this.redisInfo = this.dataService.get_RedisInformation();
        this.redisInfo.subscribe();

        this.env = environment;
        //console.log(this.env);
        //console.log(environment);
    }

    private startHttpRequest = () => {
        this.httpClient.get(environment.urlToDo + 'api/kafka')
            .subscribe(res => {
                console.log(res);
            })
    }

    removeTodo = function (item: Todo) {
        this.dataService.delete_Todos(item.id);
        this.populateTodo();
    }

    onTodoSubmit() {
        let postedTodo: Todo;

        postedTodo = {
            createdBy: this.todoForm.value.createdBy,
            createdDate: (this.todoForm.value.createdDate != null ? this.todoForm.value.createdDate : new Date()),
            dueDate: this.todoForm.value.dueDate,
            id: (this.todoForm.value.id != null ? this.todoForm.value.id : 0),
            employeeId: this.todoForm.value.employeeId,
            employeeName: this.fullNameSelected,
            todoText: this.todoForm.value.todoText
        }
        console.log(postedTodo);

        this.dataService.put_Todos(postedTodo);
        this.todoForm.reset();

        this.populateTodo();
    }

    public onChange($event) {
        this.fullNameSelected = $event.target.options[$event.target.options.selectedIndex].text;
    }

    editTodo(todo: Todo) {
        //console.log(todo);
        this.todoForm = new FormGroup({
            todoText: new FormControl(todo.todoText, Validators.required),
            dueDate: new FormControl(todo.dueDate, Validators.required),
            createdBy: new FormControl(todo.createdBy, Validators.required),
            employeeName: new FormControl(todo.employeeName),
            id: new FormControl(todo.id),
            createdDate: new FormControl(todo.createdDate),
            employeeId: new FormControl(todo.employeeId, Validators.required),
        });
    }

    private getPizzaTrackerItems(myData: any) {
        this.ptl = this.dataService.get_pizzatrackertimeline(myData).pipe();
        this.ptl.subscribe(val => console.log(val));
    }

    private getRunningBuilds(myData: any) {
        if (myData.count == 0) {
            this.buildRunning = false;
            console.log("No Running Build.");
            if (this.lastBuiltID == 0) {
                let tempRunBuild = this.dataService.get_LastRunBuild(2).pipe();
                tempRunBuild.subscribe((data) => this.lastRunBuildDetails = data);
                if (this.lastRunBuildDetails != undefined) {
                    this.lastBuiltID = this.lastRunBuildDetails.value[0].id;
                }
            }
            else {
                this.ptld = this.dataService.get_BuildLogs(this.lastBuiltID).pipe();
                this.ptld.subscribe((data) => this.getPizzaTrackerItems(data)); this.ptld = '';
            }
        } else {
            this.buildRunning = true;
            this.lastBuiltID = myData.value[0].id;
            this.ptld = this.dataService.get_BuildLogs(myData.value[0].id).pipe();
            this.ptld.subscribe((data) => this.getPizzaTrackerItems(data));
        }
    }

    private getPizzatrackertimeline() {
        this.dataService.display_Json();
        interval(2000).pipe(
            startWith(0),
            switchMap(() => this.dataService.get_RunningBuild(2))
        ).subscribe((data) => this.getRunningBuilds(data));

    }

    private populateTodo() {
        this.todoItems = this.dataService.get_Todos();
        this.todoItems.subscribe();
    }
    private populateTodoFirstLoad() {
        this.todoItems = this.dataService.get_Todos();

        var ds = this.dataService;
        this.todoItems.subscribe(result => {
            if (result.length === 0) {
                //console.log(result.length)
                this.AddressItems.subscribe(x => {
                    var num = 1;
                    x.forEach(function (y) {
                        //console.log(y.id)
                        setTimeout(() => {
                            ds.get_CreateNewEmployeeTodos(y.id, y.firstname + " " + y.lastname);
                            
                        }, num * 1000);
                        num++;
                    })                    
                })
            }
        })        
    }

  onAddressSubmit() {
    let postedAddress: Person;

    postedAddress = {
      firstname: this.addressForm.value.firstname,
      lastname: this.addressForm.value.lastname,
      streetaddress: this.addressForm.value.streetaddress,
      city: this.addressForm.value.city,
      state: this.addressForm.value.state,
      zip: this.addressForm.value.zip,
      phone: this.addressForm.value.phone,
      createdon: this.addressForm.value.createdon,
      email: this.addressForm.value.email,
      profilepicture: this.addressForm.value.profilepicture,
      id: (this.addressForm.value.id != null && this.addressForm.value.id != "" ? this.addressForm.value.id : "{00000000-0000-0000-0000-000000000000}")
    }

    this.dataService.put_Address(postedAddress);
    this.addressForm.reset();
      setTimeout(() => {
          this.populateAddresses();
          this.populateTodo();
      }, 1000);
  }

  private populateAddresses() {
    this.AddressItems = this.dataService.get_Persons().pipe(take(5));
    //console.log(JSON.stringify(this.AddressItems));
    this.AddressItems.subscribe();
  }

  editAddress(person: Person) {
    //console.log(person);

    this.addressForm = new FormGroup({
      firstname: new FormControl(person.firstname, Validators.required),
      lastname: new FormControl(person.lastname, Validators.required),
      streetaddress: new FormControl(person.streetaddress, Validators.required),
      city: new FormControl(person.city, Validators.required),
      state: new FormControl(person.state, Validators.required),
      zip: new FormControl(person.zip, Validators.required),
      phone: new FormControl(person.phone),
      createdon: new FormControl(person.createdon),
      email: new FormControl(person.email),
      profilepicture: new FormControl(person.profilepicture),
      id: new FormControl(person.id)
    });
    this.ppData = person.profilepicture;
    this.populateAddresses();
  }

  changeFile(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  public fileBlob: any;
  public ppData: any;
  uploadFile(event) {
    if (event.target.value) {
      const file = event.target.files[0];
      const type = file.type;
      this.changeFile(file).then((base64: string): any => {
        console.log(base64);
        //this.fileBlob = this.b64Blob([base64], type);
        //console.log(this.fileBlob)
        this.ppData = base64;
        this.addressForm.get('profilepicture').setValue(base64);
      });
    } else alert('Nothing')
  }

  removePerson = function (item: Person) {
      this.dataService.delete_Person(item.id);
      setTimeout(() => {
          this.populateAddresses();
          this.populateTodo();
      }, 1000);
    }
}

function uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}


 
